.record {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.record:first-of-type {
  padding-top: 0;
}

.record__description,
.record__data {
  margin: 0;
  line-height: 175%;
}

.record__description {
  font-size: 1rem;
  font-weight: normal;
}

.record__data {
  font-size: 1.2rem;
  font-weight: bold;
}

.record__delete-button {
  --size: 2rem;
  width: var(--size);
  height: var(--size);
  padding: 0.5rem;
  border: none;
  border-radius: 50%;
  background: none;
  color: white;
  cursor: pointer;
  line-height: 100%;
}

.record__delete-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
